import React, {useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import PlusRoundButton from '../../../components/common/PlusRoundButton';
import ProductIngridientLine from './ProductIngridientLine';
import {useDispatch, useSelector} from 'react-redux';
import {addProduct, deleteProduct, changeStandartisation} from '../../../actions/products/makeProductActions';
import TextFieldMedium from '../../../components/common/TextFieldMedium';
import PurpleCheckbox from '../../../components/common/PurpleCheckbox';
import IconButton from '@material-ui/core/IconButton';
import FormDeleteIcon from '../../../components/common/Icons/FormDeleteIcon';
import TextFieldLite from '../../../components/common/TextFieldLite';
import sortBy from 'lodash/sortBy';

const MakeProductFirstBlockLeft = () => {
    const classes = useStyles();
    const [standardisationDError, setStandardisationDError] = useState('');
    const [standardisationHError, setStandardisationHError] = useState('');
    const [standardisationCoverWeightError, setStandardisationCoverWeightError] = useState('');
    const ingredientsData = useSelector(
        state => state.product.makeProduct.data);
    const dispatch = useDispatch();



    const addLineFunc = () => {
        dispatch(addProduct({id:ingredientsData.products.length, name: '', percent: 100, weight: 100, price: 1}));
    };

    const deleteLine = (num) => {
        dispatch(deleteProduct(num));
    };

    const standartisationChangeHandle = (obj) => {
        if(isNaN(Number(obj.val))){
            switch (obj.elem) {
                case 'standardisationCoverWeight':
                    setStandardisationCoverWeightError('–û—à–∏–±–∫–∞!');
                    break;
                case "standardisationH":
                    setStandardisationHError('–û—à–∏–±–∫–∞!');
                    break;
                case "standardisationD":
                    setStandardisationDError('–û—à–∏–±–∫–∞!');
                    break;
            }
            return false
        } else {
            switch (obj.elem) {
                case 'standardisationCoverWeight':
                    setStandardisationCoverWeightError('');
                    break;
                case "standardisationH":
                    setStandardisationHError('');
                    break;
                case "standardisationD":
                    setStandardisationDError('');
                    break;
            }
            dispatch(changeStandartisation(obj.data));
        }
    }

    return (
        <div className={classes.makeRecipesFirstBlockLeft}>
            <TextFieldMedium label="–ù–∞–∑–≤–∞–Ω–∏–µ" required size="small"
                             variant="outlined" defaultValue="–ë–µ–ª—ã–π –±–∏—Å–∫–≤–∏—Ç"
                             className={classes.recipeField}/>
            <div className={classes.dataBlock}>
                <div className={classes.dataBlockLeftCalibr}>
                    <FormControlLabel
                        control={
                            <PurpleCheckbox
                                checked={ingredientsData.standardisation.enabled}
                                onChange={(e) => standartisationChangeHandle(
                                  {
                                      data:{...ingredientsData.standardisation, enabled: !ingredientsData.standardisation.enabled},
                                      elem: "checkedB",
                                      val: !ingredientsData.standardisation.enabled
                                  }
                                )}
                                name="checkedB"
                            />
                        }
                        label="–°—Ç–∞–Ω–¥–∞—Ä—Ç–∏–∑–∞—Ü–∏—è"
                    />
                </div>
                {ingredientsData.standardisation.enabled ? <div className={classes.dataBlockRight}>
                    <TextFieldLite
                                  label="d"
                                  variant="outlined"
                                  value={ingredientsData.standardisation.d}
                                  onChange={(e) => standartisationChangeHandle(
                                    {
                                        data:{...ingredientsData.standardisation, d: Number(e.target.value)},
                                        elem: "standardisationD",
                                        val: e.target.value
                                    }
                                  )}
                                  size="small"
                                  error={!!standardisationDError.length}
                                  helperText={standardisationDError}
                                  className={classes.recipeD}/>

                    <TextFieldLite label="h"
                                   variant="outlined"
                                   value={ingredientsData.standardisation.h}
                                   onChange={(e) => standartisationChangeHandle(
                                     {
                                         data:{...ingredientsData.standardisation, h: Number(e.target.value)},
                                         elem: "standardisationH",
                                         val: e.target.value
                                     }
                                   )}
                                   size="small"
                                   error={!!standardisationHError.length}
                                   helperText={standardisationHError}
                                   className={classes.recipeH}/>

                    <TextFieldLite label="–≤–µ—Å –ø–æ–∫—Ä—ã—Ç–∏—è"
                                   variant="outlined"
                                   value={ingredientsData.standardisation.coverWeight}
                                   onChange={(e) => standartisationChangeHandle(
                                     {
                                         data:{...ingredientsData.standardisation, coverWeight: Number(e.target.value)},
                                         elem: "standardisationCoverWeight",
                                         val: e.target.value
                                     }
                                   )}
                                   size="small"
                                   error={!!standardisationCoverWeightError.length}
                                   helperText={standardisationCoverWeightError}
                                   className={classes.recipeCoverWeight}/>
                </div> : ''}
            </div>

            {sortBy(ingredientsData.products, [function(o) { return o.id; }]).map((elem, i) => <div key={elem.id}
                                                  className={classes.dataBlockLine}>
                <FormDeleteIcon className={classes.trashIcon}
                                onClick={() => deleteLine(elem.id)}/><ProductIngridientLine elem={elem}/></div>)}

            <IconButton><PlusRoundButton addLine={addLineFunc}/></IconButton>
        </div>
    );
};

const useStyles = makeStyles((theme) => ({
    dataBlockLine: {
        display: 'flex',
        marginBottom: '15px',
        alignItems: 'center',
    },
    makeRecipesFirstBlockLeft: {
        flexBasis: '45%',
        minWidth: '335px',
        ['@media (max-width:414px)']: {
            minWidth: 'initial',
        },
    },
    recipeField: {
        width: '100%',
        marginBottom: '20px',
    },
    dataBlock: {
        display: 'flex',
        marginBottom: '15px',
        ['@media (max-width:414px)']: {
            flexWrap: 'wrap',
        },
    },
    trashIcon: {
        marginRight: '5px',
        cursor: 'pointer',

    },
    dataBlockLeftCalibr: {
        flexBasis: '45%',
        ['@media (max-width:414px)']: {
            flexBasis: '100%',
        },
    },
    dataBlockRight: {
        flexBasis: '50%',
        display: 'flex',
        ['@media (max-width:414px)']: {
            flexBasis: '100%',
        },
    },
    recipeD: {
        marginRight: '5px',
    },
    recipeH: {
        marginRight: '5px',
    },
    recipeWeight: {
        flexBasis: '50%',
    },
    recipeCoverWeight: {
        '& label': {
            top: '-10px',
        },
        '& input': {
            padding: '10.5px 4px',
        },
    },
}));

export default MakeProductFirstBlockLeft;
